.categoriesSection {
  width: 100%;
  background: #4c9cee;
  border-radius: 5px;
  padding: 7px 0;
}

.categories {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  svg {
    display: block;
    color: white;
    font-size: 45px;
    margin-left: 20px;
    @media(max-width: 450px) {
      margin-left: 10px;
    }
  }
}

.selectCategories {
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 10;
  top: 45px;
  left: 0;
}

.categoriesBtn {
  width: 200px;
  height: 45px;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 5px;
  margin: 2px 0;
  background-color: #e8e8e8;
  font-size: 18px;
  font-weight: 600;
  color: grey;
}

.categoriesBtn:hover {
  background: #4c9cee;
  color: white;
}

.categoriesBtnActive {
  background: #4c9cee;
  color: white;
}